$reactColor: #011826;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.app {
    color: white;
    background-color: $reactColor;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    h1 {
        text-transform: uppercase;
        font-family: sans-serif;
        font-size: 3rem;
    }
}
